.allcontent{
    width: 90vw;
}

.userdetails{
    margin-top: 10px;
    display: flex;
    margin-left: 25%;   
}
.leftuserdetails{
    display: flex;
    flex-direction: column;
}
.leftuserdetails img{
    height: 150px;
    width: 150px;
    border: none;
}
.leftuserdetails a{
    text-align: center;
    text-decoration: none;
}

.userrepos{
    width:80vw ;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
   
}
.repodiv{
    border: 1px solid black;
    width: 500px;  
    margin:auto;
    margin-top: 5px;
    margin-bottom:5px ;
}
.repodiv a,p,button{
    margin: 5px;
    margin-left: 10px;
    margin-bottom:5px;
    
}
.repodiv a{
    font-size:1.2rem ;
    text-decoration: none;
    font-weight: 600;
    color: rgb(2, 168, 209);
}

.repodiv button{
    font-size: 1rem;
    background-color: rgb(2, 168, 209);
    color: white;
    padding: 5px 4px ;
    border: none;
    border-radius: 4px;
    }
    .rigthusersetails h2{
        margin: 4px;
    }
    h2{
        margin-left: 10px;
    }