*{
    margin: 0%;
    padding: 0%;
}
.searchbar{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
    align-items: center;
}
.innerbox input{
    width:300px;
    height: 40px;
    padding-left: 10px;
    background-color: rgb(241, 241, 241);
    border: none;
}
.innerbox button{
    height: 40px;
    width: 100px;
    border: none;
    color: white;
    background-color: rgb(2, 168, 209);
    border-radius: 50px;
    font-size: 1rem;
    cursor: pointer;
};

